











































































import { Vue, Component } from 'vue-property-decorator'
import { delToken, getStudentToken } from 'utils/token.ts'
import Message from './Message.vue'
import { studentGetMessage } from 'utils/api'

@Component({
  components: { Message }
})
export default class Student extends Vue {
  systemName: string = '台灣免試生系統'
  collapsed: boolean = false//是否折疊（默認否）
  userName: string = ''
  userAvatar: string = ''
  //收到的消息
  hasNewMessage: boolean = false
  token: string = ''

  mounted () {
    this.token = getStudentToken()
    /*如果token不存在，則跳轉到登錄頁*/
    if (this.token == '') {
      this.$router.push('/')
    }
    // this.handleNewMessage()
    // window.setInterval(() => {
    //   setTimeout(this.handleNewMessage, 0)
    // }, 60000)//60s查詢一次消息
  }

  // handleNewMessage () {
  //   studentGetMessage()
  //     .then(res => {
  //       this.hasNewMessage = res.data.length > 0
  //     })
  //     .catch(err => {
  //       this.hasNewMessage = false
  //     })
  // }

  handleCollapse () {
    this.collapsed = !this.collapsed
  }

  backToIndex () {
    this.$router.push('/student')
  }

  logout () {
    this.$confirm('確認退出?', '提示', {})
      .then(() => {
        delToken()
        this.$router.push('/')
      })
      .catch((err) => {
        // 啥都不做
      })
  }
}



