
































import { Vue, Component } from 'vue-property-decorator'
import { allStart, getAdminEmail, setAdminEmail } from 'utils/api'

@Component({})
export default class Settings extends Vue {
  emailForm: any = {
    email: '',
    password: ''
  }
  ddlForm: any = {
    ddl: ''
  }
  rules: any = {
    email: [
      { required: true, message: '请输入邮箱', trigger: 'blur' }
    ],
    password: [
      { required: true, message: '请输入邮箱授权码', trigger: 'blur' }
    ],
    ddl: [
      { required: true, message: '请设置截止日期', trigger: 'blur' }
    ]
  }
  emailFormWidth: string = '100px'

  mounted () {
    getAdminEmail()
      .then((res) => {
        if (res.data.email === 'No admin' || res.data.admission === 'No admin') {
          this.$message({
            message: '数据不存在',
            type: 'error'
          })
        } else {
          this.emailForm.email = res.data.email
          this.emailForm.password = res.data.admission
        }
      })
      .catch((err) => {
        this.$message({
          message: err,
          type: 'error'
        })
      })
  }

  saveAdminEmail () {
    setAdminEmail({
      emailAddress: this.emailForm.email,
      admission: this.emailForm.password
    }).then((res) => {
      if (res.data.succeed) {
        this.$message({
          message: '设置成功',
          type: 'success'
        })
      } else {
        this.$message({
          message: res.data.msg,
          type: 'error'
        })
      }
    }).catch((err) => {
      this.$message({
        message: err,
        type: 'error'
      })
    })
  }

  startEnroll () {
    allStart({
      ddl: this.ddlForm.ddl
    }).then((res) => {
      if (res.data.succeed) {
        this.$message({
          message: '设置成功',
          type: 'success'
        })
      } else {
        this.$message({
          message: res.data.msg,
          type: 'error'
        })
      }
    }).catch((err) => {
      this.$message({
        message: err,
        type: 'error'
      })
    })
  }
}



