
















import { Vue, Component } from 'vue-property-decorator'

@Component({})
export default class JuniorCheck extends Vue {
  dialogVisible: boolean = false
  hasReleased: boolean = false
  hasPassed: boolean = false

  handleClose () {
    this.$confirm('確認關閉？')
      .then(_ => {
        this.dialogVisible = false
      })
      .catch(_ => {
      })
  }
}




